import Vue from 'vue';
import Router from 'vue-router';
import r1 from './r1.js';
Vue.use(Router);
export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'login',
      component: resolve => require(['@/page/login/login.vue'],resolve)
    },
    {
      path: '/a',
      name: 'logain',
      component: resolve => require(['@/page/a.vue'],resolve)
    },
    {
      path: '/details',
      name: 'details',
      component: resolve => require(['@/components/details.vue'],resolve)
    },
    {
      path: '/toplist',
      name: 'toplist',
      component: resolve => require(['@/components/toplist.vue'],resolve)
    },
    {
      path: '/play',
      name: 'play',
      component: resolve => require(['@/page/play/play.vue'],resolve)
    },
    {
      path: '/1',
      name: '1',
      component: resolve => require(['@/page/vuex/x1.vue'],resolve)
    },
    {
      path: '/2',
      name: '2',
      component: resolve => require(['@/page/vuex/x2.vue'],resolve)
    },
    {
      path: '/3',
      name: '3',
      component: resolve => require(['@/page/vuex/x3.vue'],resolve)
    },
    ...r1,
    {
    	path: '/recommend',
    	name: 'recommend',
    	component: resolve => require(['@/page/recommend/recommend.vue'],resolve),
        meta: {
            auth: true,
        },
    	redirect: '/recommend/middle',
    	children: [
    		{
    			path: 'middle',
    			name: 'middle',
                meta: {
                    auth: true,
                },
    			component: resolve => require(['@/components/middle.vue'],resolve)
    		},
    		{
    			path: 'singer',
    			name: 'singer',
                meta: {
                    auth: true,
                },
    			component: resolve => require(['@/components/singer.vue'],resolve)
    		},
    		{
    			path: 'top',
    			name: 'top',
                meta: {
                    auth: true,
                },
    			component: resolve => require(['@/components/top.vue'],resolve)
    		},
    		{
    			path: 'search',
    			name: 'search',
                meta: {
                    auth: true,
                },
    			component: resolve => require(['@/components/search.vue'],resolve)
    		}
    	]
    }
  ]
})



