import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const store = new Vuex.Store({
  state: {
    a: 1,
    curIndex: 0,
    curSong: {},
    playList: [],
    playing: true,
    alln: true,
    carList: [
      {
        sele: true,
        title: '小米笔记本Air 银色',
        price: 100,
        num: 0
      },
      {
        sele: true,
        title: '大米笔记本Air 银色',
        price: 5999,
        num: 0
      },
      {
        sele: true,
        title: '红米笔记本Air 银色',
        price: 600,
        num: 0
      },
      {
        sele: true,
        title: '小米笔记本Air 银色',
        price: 599,
        num: 0
      }
    ]
  },
  mutations:{
    add(state,index){
      state.carList[index].num++;
    },
    curIndex(state,curIndex){
    	state.curIndex = curIndex;
    },
    curSong(state,curSong){
    	state.curSong = curSong;
    },
    playList(state,playList){
    	state.playList = playList;
    },
    playing(state,playing){
    	state.playing = playing;
    }
  },
  getters: {
    curIndex(state){
      return state.curIndex;
    },
    curSong(state){
      return state.curSong;
    },
    playing(state){
      return state.playing;
    },
    playList(state){
      return state.playList[state.curIndex];
    }
  },
  actions:{

  }
})
export default store;



