<template>
	<div id="header">
		<div class="top">
			<img src="/static/images/title.gif" alt="" class='title'>
			<img src="/static/images/pop.gif" alt="" class='pop'>
		</div>
		<ul class="router">
			<li v-for='item,index in routerList'>
				<router-link :to='item.router'>{{item.con}}</router-link>
			</li>
		</ul>
	</div>
</template>
<script>
	export default{
		data(){
			return {
				routerList: [
					{
						con: '推荐',
						router: 'middle'
					},
					{
						con: '歌手',
						router: 'singer'
					},
					{
						con: '排行',
						router: 'top'
					},
					{
						con: '搜索',
						router: 'search'
					}
				]
			}
		},
		methods: {

		}
	}
</script>
<style scoped>
	*{
		margin: 0;
		padding: 0;
	}
	ul,li,ol{
		list-style: none;
	}
	a{
		text-decoration: none;
		color: #a3a3a3;
	}
	.top{
		display: table-cell;
		position: relative;
		width: 100vw;
		background: #282828;
		height: 55px;
		text-align: center;
		vertical-align: middle;
	}
	.top .title{
		width: 150px;
	}
	.top .pop{
		width: 24px;
		position: absolute;
		right: 10px;
	}
	.router{
		background: #282828;
		display: flex;
		height: 35px;
		line-height: 35px;
	}
	.router li{
		text-align:center;
		font-size: 17px;
		flex: 1;
	}
	.router li a{
		padding-bottom:3px;
		border-bottom: 2px solid #282828;
	}
	.router li .router-link-active{
		color:#fdff42;
		border-bottom: 2px solid #fdff42;
	}
</style>


